#iframeView {
  width: 100%;
  height: 100%;
  background: #f1f1f1;
}
.dark #iframeView {
  background: #0f0f0f;
}
#iframeView .docDisplay {
  width: 100%;
  height: 100%;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
}
.dark #iframeView .docDisplay {
  filter: invert();
  -webkit-filter: invert();
}

@media (min-width: 823px) {
  #youTubePlayer {
    padding-top: 0;
  }
}

#markdownDoc {
  display: flex;
  justify-content: center;
  padding-top: 0;
}
#docView {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100%);
  overflow-y: scroll;
  padding: 48px 24px;
}
#docView * {
  user-select: text;
}
#docView .docContent {
  width: 100%;
  max-width: 720px;
}

#docView h1 {
  margin-top: 36px;
  color: #1f1f1f;
}
.dark #docView h1 {
  color: #e1e1e1;
}

#docView h2 {
  margin-top: 24px;
  color: #1f1f1f;
}
.dark #docView h2 {
  color: #e1e1e1;
}

#docView p {
  font-size: 1rem;
  margin: 9px 0;
  line-height: 1.5rem;
  color: #2f2f2f;
}
.dark #docView p {
  color: #d1d1d1;
}

#docView ul {
  margin: 12px 0 12px 24px;
}
#docView ul li {
  margin: 6px 0;
  font-size: 1rem;
  list-style: disc;
  color: #2f2f2f;
}
.dark #docView ul li {
  color: #cdcdcd;
}

#docView ol {
  margin: 12px 0 12px 24px;
}
#docView ol li {
  margin: 6px 0;
  font-size: 1rem;
  list-style: decimal;
  color: #2f2f2f;
}
.dark #docView ol li {
  color: #cdcdcd;
}

#docView table {
  margin: 24px 12px;
  padding: 12px;
  background: #e9e9e9;
}
.dark #docView table {
  background: #2a2a2a;
}

#docView table thead th {
  border-bottom: 2px solid #f1f1f1;
  padding: 0 12px 6px 12px;
  color: #3f3f3f;
}
.dark #docView table thead th {
  color: #d1d1d1;
  border-bottom: 2px solid #5f5f5f;
}

#docView table tbody td {
  text-align: center;
  color: #3f3f3f;
  padding: 0 12px 6px 12px;
}
.dark #docView table tbody td {
  color: #d1d1d1;
}

.katex .katex-html {
  display: none;
}
