@media (min-width: 1px) {
  #ecSch {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding-bottom: 2rem;
    color: #3f3f3f;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    /* scroll-padding-top: calc(2rem + 54px); */
  }
  #exSch.dark {
    color: #b4b4b4;
  }

  #exSchView {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    min-width: 100%;
    width: 100%;
    max-width: 614px;
    position: relative;
    padding: 2rem 2px 0 2px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transition: 0.25s;
    -webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -ms-transition: 0.25s;
    -o-transition: 0.25s;
  }
  #exSchView > h1 {
    margin-top: 48px;
    margin-bottom: 6px;
    font-size: 2rem;
    font-weight: 900;
    color: #0000008f;
  }
  .dark #exSchView > h1 {
    color: #ffffffd1;
  }
  #exSchView > span {
    display: flex;
    position: absolute;
    align-items: center;
    top: 12px;
    padding: 6px 12px;
    margin-bottom: 24px;
    height: 36px;
    color: #ffffffa1;
    background: #0000009f;
    border-radius: 24px;
    -webkit-border-radius: 24px;
    -moz-border-radius: 24px;
    -ms-border-radius: 24px;
    -o-border-radius: 24px;
    transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -webkit-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -moz-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -ms-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -o-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  }
  .dark #exSchView > span {
    color: #000000a1;
    background: #ffffffef;
  }
  #exTable {
    display: flex;
    position: relative;
    overflow: hidden;
    width: 100%;
    white-space: nowrap;
    padding-bottom: 24px;
    left: 0;
    max-width: 720px;
    overflow-y: scroll;
  }
  .exTableRows {
    display: flex;
    flex-direction: column;
    margin-right: 0.5rem;
    width: 32%;
    height: min-content;
    min-width: 94px;
  }
  .exTableRows.top {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-right: 0.5rem;
    width: 4%;
    min-width: 48px;
    margin-right: 0;
  }
  .exTableTopTitle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    font-weight: 700;
    padding: 0.5rem;
    color: #ffffffd1;
  }
  .dark .exTableTopTitle {
    color: #ffffffa1;
  }
  .exTableLeftTitle {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
    /* margin-left: 0.75rem; */
    height: 2.4rem;
    max-width: 35px;
    padding: 0.75rem;
    font-size: 0.75rem;
    color: #ffffffd1;
    transition: 0.25s;
    -webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -ms-transition: 0.25s;
    -o-transition: 0.25s;
  }
  .dark .exTableLeftTitle {
    color: #ffffffa1;
  }
  .exTableData {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    min-height: 2.4rem;
    height: 2.4rem;
    font-size: 13.75px;
    color: #ffffff;
    padding: 0.5rem 0.7rem 0.5rem 0.7rem;
    margin-bottom: 5px;
    border-radius: 1.2rem;
    background: linear-gradient(165deg, #7ec17e, #6894af);
    box-shadow: 0 3px 5px #3f3f3f3f, 0 5px 15px #3f3f3f1f;
    -webkit-border-radius: 1.2rem;
    -moz-border-radius: 1.2rem;
    -ms-border-radius: 1.2rem;
    -o-border-radius: 1.2rem;
    transition: 0.25s;
    -webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -ms-transition: 0.25s;
    -o-transition: 0.25s;
  }
  .dark .exTableData {
    color: #d1d1d1;
    background: linear-gradient(165deg, #4e814e, #3e5c6f);
  }

  /* 多行多列塊規則 */
  .exTableData.Empty {
    opacity: 0;
  }
  .exTableData.V2 {
    height: calc(4.8rem + 5px);
  }
  .exTableData.Double {
    width: calc(200% + 0.5rem);
  }
  .exTableData.V3 {
    height: calc(7.2rem + 10px);
  }
  .exTableData.Triple {
    width: calc(300% + 1rem);
  }
  .exTableData.notEx {
    color: #5f5f5f;
    background: #ffffffa1;
  }
  .dark .exTableData.notEx {
    color: #d1d1d1;
    background: #0000008f;
  }

  #todtyDate {
    z-index: 55000;
    position: fixed;
    width: 100%;
    height: 108px;
    top: 0;
    left: 0;
    padding: 6px 9px;
    font-size: 1rem;
    font-weight: 900;
    background: #ffffff2f;
    transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -webkit-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -moz-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -ms-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -o-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  }
  .dark #todtyDate {
    background: #0000002f;
  }

  #todtyDate > span {
    display: flex;
    align-items: center;
    position: fixed;
    height: 48px;
    top: 60px;
    left: 0;
    padding: 0 18px 0 12px;
    font-size: 1rem;
    font-weight: 900;
    background: #e5e5e5;
    border-radius: 0 23px 23px 0;
    -webkit-border-radius: 0 23px 23px 0;
    -moz-border-radius: 0 23px 23px 0;
    -ms-border-radius: 0 23px 23px 0;
    -o-border-radius: 0 23px 23px 0;
    transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -webkit-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -moz-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -ms-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -o-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  }
  .dark #todtyDate > span {
    background: #1c1c1c;
  }
}

