.test {
  background-image: linear-gradient(115deg, #d7827c, #9287d0, #9bd087);
  background-image: linear-gradient(115deg, #d7827c71, #9287d071, #9bd08771);
}

#sourceFile {
  display: flex;
  align-items: center;
  position: fixed;
  left: 12px;
  bottom: calc(12px + 80px);
  padding: 16px;
  height: 36px;
  font-size: 18px;
  color: #ffffffaf;
  border-radius: 18px;
  -webkit-border-radius: 18px;
  -moz-border-radius: 18px;
  -ms-border-radius: 18px;
  -o-border-radius: 18px;
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
#sourceFile:hover {
  background: #ffffff1f;
}
#sourceFile span {
  font-size: 0.75rem;
}
@media (min-width: 823px) {
  #sourceFile {
    bottom: 12px;
    left: calc(12px + 72px);
  }
}
@media (min-width: 991px) {
  #sourceFile {
    left: calc(12px + 124px);
  }
}
